.footer{
    background: $color-primary;
    padding-top: $spacing-base*8; //32
    padding-bottom: $spacing-base*10;  //40

    a{
        color: inherit;
        position: relative;
    }
    //設定下底線
    .bottom_nav a::after,.bottom-sub-nav a::after{
        @include a-underline;
    }

    .bottom_nav a:hover::after,.bottom-sub-nav a:hover::after{
        @include a-underline_hover;
    }

    .bottom_nav{
        
        li{
            margin-right: $spacing-base*12; //48
        }
    }

    .social-media{
        font-size: $font-2xl;
        line-height: 1;
    }



    .contact{
        font-size: $font-base*2.25; //36

        li{
            margin-bottom: $spacing-base*4; //16
        }
        span{
            margin-right: $spacing-base*5.5; //22
        }
    }

   

    .bottom-sub-nav{
        border-top: 1px solid $color-light;
        li{
            margin-left: $spacing-base*12;
        }        
    }

}