
.heroImage{
    background: url(../images/banner.png) top center no-repeat;
    background-size: cover;
    height: calc(100vh - 118px); /*讓高度自適應目前畫面高度*/
    text-align: right;

    @include pad(){
        background: url(../images/banner-sm.png) top no-repeat;
        background-size: cover;
    }

    @include mobile(){
        
    }
    
    .container{
        span{
            font-size: $font-base*2.25;

            @include pad(){
                font-size: $font-md;
            }

            @include mobile(){
                font-size: $font-2xl;
            }
        }

        h2{
            font-size: $font-base*4;
            font-weight: $fw-bold;
        }
    }

}

.subTitle-2xl-c{
    font-size: $font-2xl;
    margin-bottom: $spacing-base*12;
    text-align: center;
    font-weight: $fw-bold;

    @include pad(){
        font-size: $font-xl;
        margin-bottom: $spacing-base*10;
    }

    @include mobile(){
        font-size: $font-md;
        margin-bottom: $spacing-base*6;
    }
}



.classic-series{
    .img{
        width: $col*7;
    }

    li{
        margin-bottom: $spacing-base*8;
    }
    h3{
        width: $col*5;
        padding-left:$spacing-base*8;
        font-size: $font-2xl;
        color: $color-primary;

        img{
            margin-right: $spacing-base*5.5;
        }
    }

    a{
        display: flex;
        align-items: center;   
    }

}

.co-branded{
    background: url(../images/bg.png) center;
    li{
        width: $col*6;
        text-align: center;
        color: $color-primary;
        font-weight: $fw-bold;
        font-size: $font-base*4.5;
        height: 455px;
        padding: 0 ($grid-gutter-width/2);

        &:nth-child(1){
            padding-left: 0;
    
            a{
                background-image: url(../images/product-8.png);
            }
        }

        &:nth-child(2){
            padding-right: 0;
    
            a{
                background-image: url(../images/product-9.png);
            }
        } 
    }

    a{
        color: inherit;
        display: block;
        width: 100%;
        height: 100%;
        @include bg-mask;
    }

    h3{
        padding-top: 150px;
    }

    a:hover{
        background-color: #ffffff00;
    }
}


.foreword{
    margin-bottom: $spacing-base*5;
    font-size: $font-lg;

    @include pad(){
        margin-bottom: $spacing-base*4;
        font-size: $font-base;
    }

}

%contac-input{
    border: 1px solid $color-black;
    background: none;
 }

.contact{
    .label{
        margin-bottom: $spacing-base;
    }

    .input-text{
        @extend %contac-input;
        padding: $spacing-base*3 $spacing-base*4;
        height: 50px;
    }

    .textarea{
        @extend %contac-input;
        padding: $spacing-base*3 $spacing-base*4;
    }

}

.btn-primary{
    padding-top: $spacing-base*4;
    padding-bottom: $spacing-base*4;
    background-color: $color-primary;

    &:hover{
        background: $color-secondary;
    }
}