.card-4-row-3{
    
    li{
        text-align: center;
        padding: 0 ($grid-gutter-width/2);


        @include pad(){

            &:nth-child(1),&:nth-child(3){
                margin-left: -$grid-gutter-width/2;
                padding-left: $col;
                padding-right: $grid-gutter-width;
            }
            &:nth-child(2),&:nth-child(4){
                margin-right: -$grid-gutter-width/2;
                padding-right: $col;
                padding-left: $grid-gutter-width;
            }

            &:nth-child(1),&:nth-child(2){
                margin-bottom: $spacing-base*11;
            }
        }

        @include mobile(){
            margin-left: 0;
            margin-right: 0;
            padding-left: $spacing-base*10;
            padding-right: $spacing-base*10;
        }

        img{
            width: 85px;
            height: 85px;
            margin-bottom: $spacing-base*7;
        }

        h3{
            font-size: $font-lg;
            color: $color-secondary;
            margin-bottom: $spacing-base*2; 
            font-weight: $fw-bold; 
        }
        p{
            text-align: left;
        }
    }
}

.card-4-row-4{
    display: flex;
    justify-content: space-between;
    

    @include pad(){
        flex-wrap: wrap;
    }
  
    @include mobile(){
        justify-content: center;
    }

    .card{
        box-shadow: 0px 2px 6px #00000029;
        border: -1px solid #DEE2E6;
        border-radius: 4px;
        overflow: hidden;
        max-width:$col*3;
        margin-right: ($grid-gutter-width/2);

        &:nth-child(4){
            margin-right: 0;
        }

        @include pad(){
            max-width: $col*6;
            &:nth-child(1), &:nth-child(2){
                margin-bottom: $spacing-base*8;
            }
        }
        @include mobile(){
            max-width: 100%;
            &:nth-child(1), &:nth-child(2),&:nth-child(3){
                margin-bottom: $spacing-base*4;
            }
        }
        // 要特別注意
        display: flex;
        flex-direction: column;

        img{
            width: 100%;
            height: 255px;
            object-fit: cover;
            border-radius: 4px 4px 0 0;
        }
    }
    .card-body{
        padding: $spacing-base*4;
        // 填滿上下空隙
        flex-grow:1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .card-content{
        margin-bottom: 52px !important;
    }

    .date{
        color: #707070;
    }
}

    
