.d-flex{display: flex;}

.flex-row{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.flex-column{
    display: flex;
    flex-direction: column;    
}

.flex-row-reverse{
    display: flex;
    flex-direction: row-reverse;
}

//justify-content
.justify-content-between{
    justify-content: space-between;
}

.justify-content-end{
    justify-content: flex-end;
}

.justify-content-center{
    justify-content: center;
}


//align-items
.align-items-center{
    align-items: center;
}

//flex-wrap
.flex-wrap-no{ flex-wrap: nowrap;}
.flex-wrap{flex-wrap: wrap;}