@mixin desktop{
  @media (max-width: 1140px) {
      @content;
  }
}


@mixin pad{
    @media (max-width: 768px) {
        @content;
    }
}

@mixin mobile{
    @media (max-width:375px) {
        @content;
    }
}


.container{
    max-width: 1140px; // 1110 + 30 gutter
    margin: 0 auto;
    padding: 0 ($grid-gutter-width / 2);
  
    @include pad(){ // 690 + 30 gutter
      max-width: 720px;
    }
    @include mobile(){
      max-width: 405px; //375 + 30 gutter
    }
  }

  
/* 欄位 */
.col {
  flex: 1 0 0%;
}

.col-gutter{
  padding-left: $grid-gutter-width/2;
  padding-right: $grid-gutter-width/2;
}

@each $name, $color in $cols {
  .col-#{$name} {
    width:$color;
  }
}
@include pad(){
  @each $name, $color in $cols {
    .col-md-#{$name} {
      width:$color;
    }
  }
}

@include mobile(){
  @each $name, $color in $cols {
    .col-sm-#{$name} {
      width:$color;
    }
  }
}
