/*-- size --*/
.h-100{height: 100%;}
.w-100{width: 100%;}

/*-- 置中 --*/
.m0a{ margin: 0 auto;}


/* margin-left */
@each $name, $color in $spacings {
    .ml-#{$name} {
        margin-left:$color;
    }
}
/* margin-right */
@each $name, $color in $spacings {
    .mr-#{$name} {
        margin-right:$color;
    }
}
/* margin-top */
@each $name, $color in $spacings {
    .mt-#{$name} {
        margin-top:$color;
    }
}
/* margin-bottom */
@each $name, $color in $spacings {
    .mb-#{$name} {
        margin-bottom:$color;
    }
}

/* padding-left */
@each $name, $color in $spacings {
    .pl-#{$name} {
        padding-left:$color;
    }
}
/* padding-right */
@each $name, $color in $spacings {
    .pr-#{$name} {
        padding-right:$color;
    }
}
/* padding-top */
@each $name, $color in $spacings {
    .pt-#{$name} {
        padding-top:$color;
    }
}
/* padding-bottom */
@each $name, $color in $spacings {
    .pb-#{$name} {
        padding-bottom:$color;
    }
}

@include desktop(){
    .ml-lg-0{
        margin-left: 0;
    }
    .mr-lg{
        margin-right: 0;
    }
    .mb-lg-0{
        margin-bottom: 0;
    }
    .mt-lg-0{
        margin-top: 0;
    }

  }
  

.outbox{
    padding-top: $spacing-base*15; //60
    padding-bottom: $spacing-base*20;  //80

    @include pad(){
        padding-top: $spacing-base*10;  //40
        padding-bottom: $spacing-base*14;  //56
    }

    @include mobile(){
        padding-top: $spacing-base*8;  //40
        padding-bottom: $spacing-base*13;  //56
    }
}